@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
*{font-family:"Roboto",sans-serif;font-size:16px}html,body{background-color:#61dcfb;display:flex;justify-content:center;align-items:center;width:100%;height:100%}@media screen and (max-height: 768px){html,body{align-items:flex-start}}hr{height:1px;background-color:#4c5664;border:0;margin:0}#root{width:100%;height:100%;display:flex;justify-content:center}.container{background-color:#363d47;border-radius:4px;width:50%;padding:48px;margin:auto;color:white}.container .header{display:flex;justify-content:space-between}@media screen and (max-width: 768px){.container{width:75%}}.today{font-weight:bold}.active-tasks{color:#61dcfb;font-size:12px}.text-input{padding:8px;font-size:16px;font-family:"Roboto",sans-serif;border:1px solid #4c5664;outline:none;border-radius:4px;margin:24px 0 12px 0;width:50%;color:white;background-color:#2f363e;letter-spacing:1px}.text-input:focus{background-color:#2b3139}.reset-button{background-color:#2b3139;border:0;outline:0;color:white;padding:6px 8px;cursor:pointer;border-radius:4px}.reset-button:hover{background-color:#272c33}.reset-button:focus{background-color:#20242a}.todo-item-wrapper{list-style-type:none;padding:0;margin:0;max-height:500px;overflow-y:auto}.todo-item-wrapper .todo-item{padding:16px 0;color:white;border-bottom:1px solid #4c5664;cursor:default}.hashtag{color:#61dcfb;cursor:pointer}.hashtag-filter-wrapper{min-height:40px;max-height:100px;overflow-y:auto;margin-bottom:12px;display:flex;align-items:center;flex-wrap:wrap}.hashtag-filter-wrapper .hashtag{margin-right:8px;margin-bottom:12px;padding:8px 16px;border-radius:50px;background-color:#272c33}

